.Article {
  margin: 10px;
}

.ImageContainer {
  width: 100%;
  position: relative;
  text-align: center;
}

.Image {
  opacity: 0.7;
  border-radius: 3px;
  object-fit: none;
  max-height: 600px;
  width: 100%;
}

.Title {
  font-weight: lighter;
  text-shadow: -1px -1px 0 #212121, 1px -1px 0 #212121, -1px 1px 0 #212121,
    1px 1px 0 #212121;
}

.ArticleInfomation {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.Date {
  text-align: center;
  font-size: 15px;
  font-weight: bold;
  color: red;
}

.ArticleMain {
  padding: 40px;
  margin-left: 0;
  width: 100%;
  text-align: left;
}
